import base from './index'
let axios = base.axios
let baseUrl = base.baseUrl

// Ëé∑ÂèñÂ•ΩÂèã
export const getFriend = params => {
    return axios({
      method: 'post',
      baseURL: `${baseUrl}/friend/friendList`,
      data: params
    }).then(res => res.data)
  }

  // Ëé∑ÂèñËÅäÂ§©‰ø°ÊÅØ
export const getChatMsg = params => {
  return axios({
    method: 'post',
    baseURL: `${baseUrl}/friend/chatMsg`,
    data: params
  }).then(res => res.data)
}