  export function judgeFileType(file) {
    if (file == null||file == ""){  
         alert("ËØ∑ÈÄâÊã©Ë¶Å‰∏ä‰º†ÁöÑÂõæÁâá!");  
         return false;  
    }  
    if (file.lastIndexOf('.')==-1){    //Â¶ÇÊûú‰∏çÂ≠òÂú®"."    
        alert("Ë∑ØÂæÑ‰∏çÊ≠£Á°Æ!");  
        return false;  
    }  
    var AllImgExt=".jpg|.jpeg|.gif|.bmp|.png|";  
    var extName = file.substring(file.lastIndexOf(".")).toLowerCase();//ÔºàÊääË∑ØÂæÑ‰∏≠ÁöÑÊâÄÊúâÂ≠óÊØçÂÖ®ÈÉ®ËΩ¨Êç¢‰∏∫Â∞èÂÜôÔºâ          
    if(AllImgExt.indexOf(extName+"|")==-1)          
    {  
        ErrMsg="ËØ•Êñá‰ª∂Á±ªÂûã‰∏çÂÖÅËÆ∏‰∏ä‰º†„ÄÇËØ∑‰∏ä‰º† "+AllImgExt+" Á±ªÂûãÁöÑÊñá‰ª∂ÔºåÂΩìÂâçÊñá‰ª∂Á±ªÂûã‰∏∫"+extName;  
        alert(ErrMsg);  
        return false;  
    }  
  }

